<template>
  <div>
    <div class="row">
      <div class="col-xs-12 login-form">
        <div class="form-group col-sm-6 col-xs-12 ">
          <input type="text" class="form-control login-field" placeholder="日期（yyyy-mm-dd）" name="timerange" v-model="timerange" id="login-name" />
          <label class="login-field-icon fui-calendar-solid" for="login-name">&nbsp;请输入日报日期&nbsp;&nbsp;</label>
        </div>
        <div class="content">
          <div class="btn btn-lg btn-primary" @click="exportReport()">导出</div>
        </div>
      </div>

      <div class="col-xs-12 login-form">
        <div class="form-group col-sm-6 col-xs-12 ">
          <input type="text" class="form-control login-field" placeholder="日期（yyyy-mm-dd）" name="timerange" v-model="timerange"/>
          <label class="login-field-icon fui-calendar-solid">&nbsp;请输入日报日期&nbsp;&nbsp;</label>
        </div>
        <div class="content">
          <div class="btn btn-lg btn-primary" @click="exportWeekReport()">获取周报</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Base from './base.js';

export default {
  name: 'export',
  mixins: [Base],
  data () {
    return {
      timerange: this.getToday()
    }
  },
  components:{
  },
  mounted(){
  },
  methods: {
    getToday(){
      var date = new Date();
      var seperator1 = "-";
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var strDate = date.getDate();
      if (month >= 1 && month <= 9) {
          month = "0" + month;
      }
      if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate;
      return currentdate;
    },
    exportReport(){
      window.location = '/dayreport/api/export?timerange=' + this.timerange;
    },
    exportWeekReport(){
      var that = this;
      $.ajax({
        url: '/dayreport/api/exportweek',
        data: {
          token: that.token,
          timerange: that.timerange
        },
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          if(res.code != 200){
            that.$wii.toast.show({
              text: res.msg,
              type: 'warn',
              time: 1500
            });
          }else{
            console.log(res.data);
          }
        }
      })
    }
  }
}
</script>
<style lang="less">
.btn-group{
  margin-top: 15px;
}
</style>



